const _import = require('../utils/view-import/' + process.env.NODE_ENV);

export default {
    routes: [
        {
            path: '/',
            redirect:'/login',
        },
        {
            path: '/login',
            component: _import('login'),
        },
        {
            path: 'index',
            component: _import('index'),
            children :[
                {
                    path: '/home',
                    component: _import('home/index'),
                },
                {
                    path: '/companyCenter',
                    component: _import('companyCenter/menu'),
                    children:[
                        {
                            path: '/companyCenterMain',
                            component: _import('companyCenter/view/index'),
                        },
                        {
                            path: '/companyCenterManage',
                            component: _import('companyCenter/view/companyCenterManage'),
                        },
                      {
                        path: '/baseDataView',
                        component: _import('companyCenter/view/baseDataIndex'),
                        redirect: '/whiteList',
                        children: [
                          {
                            path: '/addBusiness_qg',
                            component: _import('companyCenter/component/baseData/addBusiness'),
                          },
                          {
                            path: '/businessConfig_qg',
                            component: _import('companyCenter/component/baseData/businessConfig'),
                          },
                          {
                            path: '/blacklist',
                            component: _import('companyCenter/component/baseData/blacklist/blacklistData'),
                          },
                          {
                            path: '/whiteList',
                            component: _import('companyCenter/component/baseData/whiteList/whiteData'),
                          },
                          {
                            path: '/editAndUp',
                            component: _import('companyCenter/component/baseData/editAndUpUphold/editAndUps'),
                          },
                        ]
                      },
                        {
                            path: '/regulation',
                            component: _import('companyCenter/view/zhishiku'),
                            children:[
                               {
                                path: '/regulationTable',
                                component: _import('companyCenter/component/menu/table'),
                               }
                            ]
                        },
                    ]
                },
                {
                    path: '/riskControlCenter',
                    component: _import('riskControlCenter/index'),
                    children :[
                      {
                          path: '/overView',
                          component: _import('riskControlCenter/view/overView'),
                      },
                      {
                        path: '/overManage',
                        component: _import('riskControlCenter/view/overManage'),
                      },
                      {
                        path: '/overIncompatible',
                        component: _import('riskControlCenter/view/overIncompatible'),
                      },
                      {
                        path: '/supervisionPlan',
                        component: _import('riskControlCenter/view/supervisionPlan'),
                      },
                      {
                        path: '/supervisionPlanExecution',
                        component: _import('riskControlCenter/view/supervisionPlanExecution'),
                      },
                      {
                        path: '/supervisionPlanStatistics',
                        component: _import('riskControlCenter/view/supervisionPlanStatistics'),
                      }
                    ]
                },
                {
                    path: '/disposalCenter',
                    component: _import('disposalCenter/index'),
                    children :[
                      {
                          path: '/disposalCenterHome',
                          component: _import('disposalCenter/view/home'),
                      },
                      {
                          path: '/defectView',
                          component: _import('disposalCenter/view/defectView'),
                      },
                      {
                          path: '/details',
                          component: _import('disposalCenter/view/details'),
                      }
                    ]
                },
                {
                    path: '/evaluationCenter',
                    component: _import('evaluationCenter/index'),
                    children :[
                      {
                          path: '/totalView',
                          component: _import('evaluationCenter/view/totalView'),
                      },
                      {
                          path: '/tableReportView',
                          component: _import('evaluationCenter/view/tableReportView'),
                      },
                      {
                          path: '/tableDefect',
                          component: _import('evaluationCenter/view/table-defect'),
                      }
                    ]
                },
                {
                    path: '/personalCenter',
                    component: _import('personalCenter/index'),
                    children :[
                      {
                          path: '/ruleManagement',
                          component: _import('personalCenter/view/myTodoList/ruleManagement'),
                      },
                      {
                          path: '/knowledgeManagement',
                          component: _import('personalCenter/view/myTodoList/knowledgeManagement'),
                      },
                      {
                          path: '/complianceManagement',
                          component: _import('personalCenter/view/myTodoList/complianceManagement'),
                      },
                      {
                          path: '/ruleList',
                          component: _import('personalCenter/view/myTodoList/complianceManagement/ruleList'),
                      },
                      {
                          path: '/minorDefectManagement',
                          component: _import('personalCenter/view/myTodoList/minorDefectManagement'),
                      },
                      {
                          path: '/supervisionPlanManagement',
                          component: _import('personalCenter/view/myTodoList/supervisionPlanManagement'),
                      },
                    ]
                },
                {
                    path: '/systemLog',
                    component: _import('systemLog/index'),
                    children :[
                      {
                          path: '/operationLog',
                          component: _import('systemLog/view/operationLog'),
                      },
                      {
                          path: '/signInLog',
                          component: _import('systemLog/view/signInLog')
                      },
                    ]
                },
            ]
        }
    ]
}
